import React from "react"

export default function QiwiUrl(props) {
  const publicKey = `48e7qUxn9T7RyYE1MVZswX1FRSbE6iyCj2gCRwwF3Dnh5XrasNTx3BGPiMsyXQFNKQhvukniQG8RTVhYm3iPyXiqJauFTVWrkPBA4D8ecfvczN2yVDoBS5dUJFK1qpZv6hpDnJKhBv8o7yAFyWpWAx13gS9szyjHosJYiyWj8s7Ub4ea59TdwTBVTq3wr`
  const billId = props.billId
  const amount = props.amount
  const email = encodeURI(props.email)
  const comment = encodeURI(props.comment)
  const successUrl = encodeURI(`https://vk.com`)
  let payUrl = `https://oplata.qiwi.com/create?publicKey=${publicKey}&billId=${billId}&amount=${amount}&email=${email}&comment=${comment}&successUrl=${successUrl}`

  return (
    <>
      <a href={payUrl}>Qiwi url</a>
    </>
  )
}
